import request from '@/utils/request'

//------------------------------------------------------------------------------
/// =1=10
export function getLists(params) {   //sellerlevel='3'
  return request({
    url:'/member/info/getList',
    method:'get',
    params:params
  })
}

//
export function fetchList(params) {
  return request({
    url:'/member/info/search',
    method:'get',
    params:params
  })
}

// -
export function getUserDetail(params) {
  return request({
    url:'/member/info/getDetail',
    method:'get',
    params:params
  })
}

//  post
export function updateUser(data) {
  return request({
    url:'/member/info/modify/',
    method:'post',
    data:data
  })
}

//
export function fetchUserAddr(params) {
  return request({
    url:'/member/address/getListByUser',
    method:'get',
    params:params
  })
}

//
export function deleteUser(params) {
  return request({
    url:'/member/info/delete',
    method:'get',   //postget???????????????
    params:params
  })
}
