//----------------------------------------------------------------------------------
//  
export function getDiscountSetting(id) {
  return request({
    url:'/orderSetting/'+id,
    method:'get',
  })
}

//  
export function updateDiscountSetting(id,data) {
  return request({
    url:'/orderSetting/update/'+id,
    method:'post',
    data:data
  })
}


