<template>
  <el-card class="form-container" shadow="never" style="background:#f2f2f2;">
    <el-form   
             ref="orderSettingForm"
             :model="orderSetting"  :rules="rules"
             label-width="150px">
      <el-form-item label="" prop="normalOrderOvertime">
        <el-input v-model="orderSetting.normalOrderOvertime" class="input-width" clearable>
          <template slot="append"></template>
        </el-input>
        <span class="note-margin"></span>
      </el-form-item>
      <el-form-item label="" prop="confirmOvertime">
        <el-input v-model="orderSetting.confirmOvertime"  class="input-width" clearable>
          <template slot="append"></template>
        </el-input>
        <span class="note-margin"></span>
      </el-form-item>
      <el-form-item label="" prop="finishOvertime">
        <el-input v-model="orderSetting.finishOvertime" class="input-width" clearable>
          <template slot="append"></template>
        </el-input>
        <span class="note-margin"></span>
      </el-form-item>
      <el-form-item label="" prop="commentOvertime">
        <el-input v-model="orderSetting.commentOvertime" class="input-width" clearable>
          <template slot="append"></template>
        </el-input>
        <span class="note-margin"></span>
      </el-form-item>
      <el-form-item>
        <el-button
          @click="confirm('orderSettingForm')"
          type="primary"></el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
import { getorderSetting,updateorderSetting } from "@/api/orders";

const defaultorderSetting = {
    normalOrderOvertime:null,
    confirmOvertime:null,
    finishOvertime:null,
    commentOvertime:null,
  };

export default {
    data(){
      return{
        orderSetting: Object.assign({}, defaultorderSetting),
      }
    },
    created(){
      this.getDetail();
    },
    methods:{
      //
      getDetail(){
        getorderSetting().then(response=>{
          this.orderSetting=response.data;
        })
      },

      //
      confirm(){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          updateorderSetting(this.orderSetting).then(response=>{
            this.$message({
              type: 'success',
              message: '!',
              duration:1000
              });
            })
        });
      },


    }
}
</script>

<style scoped>
    .input-width {
    width: 50%;
  }

  .note-margin {
    margin-left: 15px;
  }
</style>



