<template>
    <div class="app-container">
<!-- S-->
        <el-card shadow="never" style="background:#f2f2f2;">
            <div>
                <i class="el-icon-search"></i>
                <span></span>
                <el-button
                    style="float: right"
                    @click="handleSearchList()"
                    type="primary"
                    size="small">
                    
                </el-button>
                <!---->
                <!-- <el-button
                    style="float: right;margin-right: 15px"
                    @click="handleResetSearch()"
                    size="small">
                    
                </el-button> -->
            </div>
            <div style="margin-top: 10px">
                <el-form :inline="true" :model="listQuery" size="small" label-width="140px">
                    <el-form-item label="">
                        <el-input style="width: 203px" v-model="listQuery.username" placeholder=""></el-input>
                    </el-form-item>
                    <!---->
                    <!-- <el-form-item label="">
                        <el-date-picker
                        style="width: 373px"
                        v-model="listQuery.SearchDate"
                        type="datetimerange"
                        range-separator=""
                        start-placeholder=""
                        end-placeholder=""
                        :picker-options="pickerOptions1">
                        </el-date-picker>
                    </el-form-item> -->
                </el-form> 
            </div>
        </el-card>
<!-- E-->

<!-- S-->            
        <el-table
        highlight-current-row
        :header-cell-style="{background:'#f2f2f2',color:'#606266','border-bottom': '1px rgb(103, 194, 58) solid'}"
        :data="listRecord"
        style="width: 100%"
        @selection-change="handleSelectionChange">
            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand" label-width="100px">
                        <!-- <el-form-item label="">
                            <span>{{ props.row.name }}</span>
                        </el-form-item> -->
                        <el-form-item label="">
                            <span>{{ props.row.poductName }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.productPrice }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.productQuantity }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.orderMoeny }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.orderStatus }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.firstAddr }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.createTime | dateFormatter}}</span>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>
            <el-table-column
                type="selection"
                width="55">
            </el-table-column>
            <el-table-column
                label=""
                align='center'
                prop="userInfoId">
            </el-table-column>
            <el-table-column
                label=""
                align='center'
                prop="username">
            </el-table-column>
            <el-table-column
                label=""
                align='center'
                prop="poductName">
            </el-table-column>
            <el-table-column
                label=""
                align='center'
                prop="orderNumber">
            </el-table-column>
            <!-- <el-table-column
                label=""
                align='center'
                prop="level">
            </el-table-column> -->
            <!-- <el-table-column label="" width="160" align="center">
                <template slot-scope="scope">
                    <p>
                    <el-button
                        size="mini"
                        @click="handleUpdateUser(scope.$index, scope.row)">
                    </el-button>
                    </p> 
                    <p>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">
                    </el-button>
                    </p>
                </template>
            </el-table-column> -->
        </el-table>
<!-- E-->            
        <div class="pagination-container">
            <el-pagination
                background
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                layout="total, sizes,prev, pager, next,jumper"
                :current-page.sync="listQuery.pageNum"
                :page-size="listQuery.pageSize"
                :page-sizes="[5,10,15]"
                :total="total">
            </el-pagination>
        </div>
        <!-- <div class="Pagination" style="text-align: left;margin-top: 10px;">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="20"
                layout="total, prev, pager, next"
                :total="count">
            </el-pagination>
        </div> -->
    </div>
</template>

<script>
import {fetchList} from '@/api/user'  //member
import {formatDate} from '@/utils/date';
const defaultListQuery = {
    pageNum: 1,
    pageSize: 10,
    
    // name:null,
    username:null,
    // phoneNumber:null,
    // identityCardNo:null,
    // level:null,
    SearchDate:null
    
  };

export default {

    // 
    filters: {
        //
        dateFormatter(time) {
            let date = new Date(time);
            return formatDate(date, 'MM.dd.yyyy hh:mm:ss')
        },
    },    
    data(){
        return{
            listQuery: Object.assign({}, defaultListQuery),
            listRecord: [
                { 
                    userInfoId:'12323324',
                    username:'shdshdshdasd',
                    poductName:'',
                    createTime:'2010-8-7',
                    name:'',
                    productPrice:'2000',
                    productQuantity:'3',
                    orderMoeny:'6000',
                    orderStatus:'',
                    firstAddr:'dwudduygdyffffffffff',
                    orderNumber:'2343454646',
                },{
                    userInfoId:'12323324',
                    username:'shdshdshdasd',
                    poductName:'',
                    createTime:'2010-8-7',
                    name:'',
                    productPrice:'2000',
                    productQuantity:'3',
                    orderMoeny:'6000',
                    orderStatus:'',
                    firstAddr:'dwudduygdyffffffffff',
                    orderNumber:'2343454646',
                },
                {
                    userInfoId:'12323324',
                    username:'shdshdshdasd',
                    poductName:'',
                    createTime:'2010-8-7',
                    name:'',
                    productPrice:'2000',
                    productQuantity:'3',
                    orderMoeny:'6000',
                    orderStatus:'',
                    firstAddr:'dwudduygdyffffffffff',
                    orderNumber:'2343454646',
                }
            ],
            //listLoading: true,
            
            // offset: 0,
            // limit: 20,
            // count: 0,
            //currentPage: 1,

            pickerOptions1: {
            shortcuts: [{
                text: '',
                onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit('pick', [start, end]);
                }
            }, {
                text: '',
                onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                picker.$emit('pick', [start, end]);
                }
            }, {
                text: '',
                onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                picker.$emit('pick', [start, end]);
                }
            }]
            },
            SearchDate:'',
        }
    },
    methods:{

        //
        // handleResetSearch(){
        //     this.listQuery = Object.assign({}, defaultListQuery);
        // },

        //
        getSearchList(){
            this.listLoading=true;
            //this.listQuery
            fetchList(this.listQuery).then(response => {
                this.listLoading = false;
                this.list = response.data.list;
                this.total = response.data.total;
            });
        },

        //
        handleSearchList(val){
            this.listQuery.pageNum = 1;
            this.listQuery.pageSize = val;
            this.getSearchList();
        },
        
        //handleShowUser(){},
        //handleUpdateUser(){},
        //handleDelete(){},

        handleSelectionChange(){},

        //
        handleSizeChange(val){
            this.listQuery.pageNum = 1;
            this.listQuery.pageSize = val;
            // this.getList();       ?????????????????
        },
        //
        handleCurrentChange(val) {
            this.listQuery.pageNum = val;
            // this.getList();        ????????????????????
        },
    }
}
</script>

<style scoped>
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
</style>







