<template>
    <div class="app-container">
<!-- S-->        
        <el-card class="box-card" :class="showInfo? 'show': 'hide'"> 
            <div slot="header" class="clearfix" @click="show">
                <el-button style="float: left; padding: 3px 0" type="text"></el-button>
            </div>
            <div class="text item" v-if="showInfo">
                <p>1</p>
                <p>2</p>
                <p>3</p>
                <p>4</p>
            </div>
        </el-card>
<!-- E-->      
<!-- S-->
        <div class="table-container">
            <el-form :inline="true" :model="AddManagerForm" status-icon :rules="Vrules" ref="AddManagerForm" label-width="100px" >
                <el-form-item label=":"  prop="username">
                    <el-input clearable  v-model="AddManagerForm.username" style="width:203px" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="name">
                    <el-input clearable  v-model="AddManagerForm.name" style="width:203px" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="password">
                    <el-input type="password" v-model="AddManagerForm.password" style="width:203px" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="checkPass">
                    <el-input type="password" v-model.number="AddManagerForm.checkPass" style="width:203px"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="phoneNumber">
                    <el-input clearable  v-model.number="AddManagerForm.phoneNumber" style="width:203px" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="email">
                    <el-input clearable  v-model="AddManagerForm.email" style="width:203px" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="identityCardType">
                    <el-select v-model="AddManagerForm.identityCardType" placeholder="" style="width:203px">
                    <el-option label="" value="IDcard"></el-option>
                    <el-option label="" value="Passcard"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label=":" prop="identityCardNo">
                    <el-input clearable v-model="AddManagerForm.identityCardNo" autocomplete="off" style="width:203px"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="level">
                    <el-select v-model="AddManagerForm.level" placeholder="" style="width:203px">
                    <el-option label="" value="shanghai"></el-option>
                    <el-option label="" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label=":">
                    <el-date-picker
                        class="input-width"
                        v-model="AddManagerForm.birthday"
                        value-format="yyyy-MM-dd"
                        type="date"
                        style="width:203px"
                        placeholder="">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label=":">
                    <el-radio-group v-model="AddManagerForm.gender" style="width:203px">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item class="but">
                    <el-button type="primary" @click="submitForm('AddManagerForm')"></el-button>
                    <el-button @click="resetForm('AddManagerForm')"></el-button>
                </el-form-item>
            </el-form>
        </div>
<!-- E-->
    </div>
</template>

<script>
import {AddManager} from '@/api/admin'

const defaultAddManagerForm = {
    username:'',
    name:'',
    password: '',
    checkPass: '',
    phoneNumber:'',
    identityCardType:'',
    identityCardNo:'',
    level:'',
    gender:'',
    email: '',
    birthday:'', 
  };
export default {
    data(){

      //
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.AddManagerForm.checkPass !== '') {
            this.$refs.AddManagerForm.validateField('checkPass');
          }
          callback();
        }
      };

      //
      var validateCheckPass = (rule, value, callback) => {
        if (this.AddManagerForm.password === '') {
          callback(new Error(''));
        } else if(value === '') {
          callback(new Error(''));
        } else if (value != this.AddManagerForm.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };

      //
      var checkphoneNumber = (rule, value, callback) => {
        if(this.AddManagerForm.phoneNumber === '') {
          callback(new Error(''));
        } else if (!Number.isInteger(value)) {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      // else if (value!=/^1[34578]\d{8}$/) {
      //     callback(new Error('10'));
      //   }

      //
      //  var checkphoneNumber = (rule, value, callback) => {
      //   if (!value) {
      //     return callback(new Error(''));
      //   }
      //   setTimeout(() => {
      //     if (!Number.isInteger(value)) {
      //       callback(new Error(''));
      //     } else {
      //       if (length(value) != 10) {
      //         callback(new Error('10'));
      //       } else {
      //         callback();
      //       }
      //     }
      //   }, 1000);
      // };

      //
      // var checkNo = (rule, value, callback) => {
      //   // alert(typeof value);//valuestring
      //   // console.log(typeof value)
      //   if (!value) {
      //     callback(new Error(''));
      //   } else if (!Number.isInteger(value)) {
      //       callback(new Error(''));
      //     } 
      //     else {
      //       callback();
      //     }
      // };

      return {
        showInfo: false,  
        AddManagerForm: Object.assign({}, defaultAddManagerForm),
        Vrules: {
          username: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 16, message: ' 3  16 ', trigger: 'blur' }
          ],
          name:[
            { required: true, message: '', trigger: 'blur' }
          ],
          password: [
            { validator: validatePass, trigger: 'blur' },
            { min: 8, max: 16, message: ' 8  16 ', trigger: 'blur' }
          ],
          checkPass: [
            { validator: validateCheckPass, trigger: 'blur' }
          ],
          phoneNumber:[
            { validator: checkphoneNumber, trigger: 'blur' },
          ],
          identityCardNo:[
            { required: true, message: '', trigger: 'blur' }
          ],
          identityCardType:[
            { required: true, message: '', trigger: 'change' }
          ],
          level:[
            { required: true, message: '', trigger: 'change' }
          ],
          gender:[
            { required: true, message: '', trigger: 'change' }
          ],
          birthday:[
            { type: 'array', required: true, message: '', trigger: 'change' },
          ],
          email:[
            { required: true, message: '', trigger: 'blur' },
            { type: 'email', message: '', trigger: ['blur', 'change'] }         
          ],
        }
      };

    },

    methods:{

// 
        show(){
                this.showInfo = !this.showInfo
            },

//
        submitForm(formName) {
        this.$refs[formName].validate((valid) => {
            // if (valid) {
            //     alert('submit!');
            //     } else {
            //     console.log('error submit!!');
            //     return false;
            //     }
            //
              AddManager(this.AddManagerForm).then(response=>{
                this.$message({
                  message: '',
                  type: 'success',
                  duration:1000
                  });
                  this.$router.push({path: '/admin/list_admin'})  
                });
            });
        },

        // //
        // submitForm(){     //
        //   AddManager(this.AddManagerForm).then(response=>{
        //     this.$message({
        //       message: '',
        //       type: 'success',
        //       duration:1000
        //       });
        //       this.$router.push({path: '/admin/list_admin'})  
        //     });
        // },

        //
        resetForm(formName) {
          this.$refs[formName].resetFields();
          this.AddManagerForm = Object.assign({}, defaultAddManagerForm);
        }
    },
}
</script>

<style scoped>
    /* .text {
        font-size: 14px;
    } */
    .item {
        margin-bottom: 18px;
        /* padding: 20px; */
    }
    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }
    .box-card {
        width: 990px;
    }
    .show{
		height: 220px;
	}
    .hide{
        height: 59px;
    }
    .text p{
        padding: 5px;
    }
    .table-container{
        border: 1px solid #ebeef5;
        padding: 20px;
        width: 95%;
    }
</style>



