<template>
  <div class="app-container">
    <el-card class="filter-container" shadow="never" style="background:#f2f2f2;">
      <div>
        <i class="el-icon-search"></i>
        <span></span>
        <el-button
          style="float:right"
          type="primary"
          @click="handleSearchList()"
          size="small">
          
        </el-button>
        <el-button
          style="float:right;margin-right: 15px"
          @click="handleResetSearch()"
          size="small">
          
        </el-button>
      </div>
      <div style="margin-top: 15px">
          <!-- :model="listQuery" -->
        <el-form :inline="true"      size="small" label-width="140px">
          <el-form-item label="">
            <!-- v-model="listQuery.productName" -->
            <el-input      class="input-width" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <!-- v-model="listQuery.productName" -->
            <el-input      class="input-width" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>

    <el-table
    :data="tableData5"
    highlight-current-row
    style="width: 100%">
    <el-table-column type="expand">
      <template slot-scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="">
            <span>{{ props.row.name }}</span>
          </el-form-item>
          <!-- <el-form-item label="">
            <span>{{ props.row.shop }}</span>
          </el-form-item> -->
          <el-form-item label="">
            <span>{{ props.row.id }}</span>
          </el-form-item>
          <!-- <el-form-item label=" ID">
            <span>{{ props.row.shopId }}</span>
          </el-form-item> -->
          <el-form-item label="">
            <span>{{ props.row.category }}</span>
          </el-form-item>
          <!-- <el-form-item label="">
            <span>{{ props.row.address }}</span>
          </el-form-item> -->
          <el-form-item label="">
            <span>{{ props.row.desc }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      prop="id">
    </el-table-column>
    <el-table-column
      label=""
      prop="name">
    </el-table-column>
    <el-table-column
      label=""
      prop="desc">
    </el-table-column>
    <el-table-column label="" width="160" align="center">
        <template slot-scope="scope">
            <!-- <p>
            <el-button
                size="mini"
                @click="handleShowUser(scope.$index, scope.row)">
            </el-button>
            <el-button
                size="mini"
                @click="handleUpdateUser(scope.$index, scope.row)">
            </el-button>
            </p> -->
            <p>
            <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)">
            </el-button>
            </p>
        </template>
    </el-table-column>
  </el-table>
  </div>        
</template>

<script>
export default {
    data(){
        return {
            tableData5: [{
            id: '12987122',
            name: '',
            category: '',
            desc: '',
            // address: '',
            // shop: '',
            // shopId: '10333'
            },
            {
            id: '12987126',
            name: '',
            category: '',
            desc: '',
            // address: '',
            // shop: '',
            // shopId: '10333'
            }]
        }
    },
    methods(){},
    
}
</script>
<style scoped>
    .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }

</style>


    


